body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
  background: #43dde6;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container{
  background: #364f6b;
  width: 80vh;
  height: 70vh;
  box-shadow: 5px 5px 9px #888888;
  border-radius: 20px;
  text-align: center;
  padding: 5px;
}

.title{
  color: #fc5185;
  font-size: 50px;
  font-family: cursive;
  text-transform: uppercase;
}

.inp{
  width: 100px;
  height: 30px;
  border-radius: 5px;
  padding-left: 10px;
  margin-right: 10px;
  color: #000;
}

.btn{
  width: 112px;
  height: 37px;
  border-radius: 25px;
  padding-left: 10px;
  margin-right: 10px;
  color: #fc5185;
  font-weight: bold;
}

.inp:active , .btn:active{
  outline: none;
}

.list{
  color: #43dde6;
  margin: 30px;
  text-align: left;

}

.item{
  margin-bottom: 10px;
  font-size: 20px;
  border-bottom: double;
  border-color: #f0f0f0;
}